import React, {createContext, useReducer, Dispatch} from 'react';


interface State {
    apiUrl: string
    mapKey: string

    mobileOpen: boolean
    loggedIn: boolean
}

interface StateContextType {
    state: State,
    dispatch: Dispatch<any>
}

const initialState = {
    apiUrl: 'http://localhost:8000',

    mapKey: 'AIzaSyDpJTRRgBI70kyCK83ReUPHKa_oxivp9u0',
    
    mobileOpen: false,
    loggedIn: false,
};


const mainStore = createContext<StateContextType>({state: initialState, dispatch: () => {}});
const { Provider } = mainStore;

const MainStateProvider = ( { children }: any ) => {

    const [state, dispatch] = useReducer((state: State, action: any) => {
        switch(action.type) {
        case 'setFilterPlaceOpen':
            return {
                ...state,
                filterPlaceOpen: action.filterPlaceOpen
            };
        case 'setFilterMenuFull':
            return {
                ...state,
                filterMenuFull: action.filterMenuFull
            };
        case 'closeFilters':
            return {
                ...state,
                filterMenuOpen: false,
                filterMenuFull: false
            };
        case 'openFilters':
            return {
                ...state,
                filterMenuOpen: true,
            };
        case 'login':
            localStorage.setItem('access_token', action.accessToken);
            localStorage.setItem('refresh_token', action.refreshToken);
            return {
                ...state,
                loggedIn: true,
                username: action.username
            };
        case 'logout':
            localStorage.removeItem('access_token');
            localStorage.removeItem('refresh_token');
            return {
                ...state,
                loggedIn: false
            };
        case 'setMobileOpen':
            return {
                ...state,
                mobileOpen: action.mobileOpen
            };

        case 'setPlaceFilters':
            return {
                ...state,
                placeFilters: action.placeFilters
            };

        case 'setEventFilters':
            return {
                ...state,
                eventFilters: action.eventFilters
            };

        case 'setEventDate':
            return {
                ...state,
                eventFilterStartDate: action.startDate,
                eventFilterEndDate: action.endDate,
                eventFilterDateType: action.dateType

            };
        default:
            throw new Error();
        };
    }, initialState);

    const value = { state, dispatch };

    return <Provider value={value}>{children}</Provider>;
};

export { mainStore, MainStateProvider, initialState }