import React, { useEffect, useRef, useState } from 'react';
import styled from 'styled-components'
import FirstPage from './FirstPage'
import NavBar from '../components/NavBar'


import AboutPage from './AboutPage'
import GalleryPage from './GalleryPage'
import MeetUsPage from './MeetUsPage'
import EventsPage from './EventsPage';
import ContactPage from './ContactPage';
import TestPage from './TestPage'

const Container = styled.div`
    position: relative;
    height: 100%;
    width: 100%;
    background: white;
    overflow-x: hidden;
    overflow-y: auto;
    scroll-behavior: smooth;
`

//    scroll-snap-type: y mandatory;

const height  = window.innerHeight

interface propTypes {

}
function MainPage(props: propTypes) {

    const [pageScroll, setPageScroll] = useState(0)
    const scrollRef = useRef<HTMLDivElement>(null)
    useEffect(() => {
        let dragElemRef = scrollRef.current
        if(dragElemRef) {
            dragElemRef.addEventListener('scroll', function(){
                let elem = document.getElementById("container")
                if(elem !== null) {
                    let scrollOff = elem.scrollTop
                    setPageScroll(scrollOff)
                }
            })
        }
    }, [])

    return (
        <Container
            ref={scrollRef}
            id="container"
        >
            <FirstPage />
            <NavBar setScroll={setScroll}/>

            <AboutPage 
                open={pageScroll > (height * 0.35)}
            
            />
            <MeetUsPage />
            <GalleryPage />
            <EventsPage />
            <ContactPage />
            
        </Container>
    );

    function setScroll(top: number) {
        let scroller = document.getElementById("container")
        if(scroller) 
            scroller.scrollTop = top
    }
}

export default MainPage;