import React, {useEffect, useState, useContext} from 'react';
import logo from './logo.svg';
import './App.css';
import styled from 'styled-components'
import { mainStore, MainStateProvider } from './store/MainStore'
import history from './history'
import { Router } from 'react-router-dom'

import Eyes from './components/Eyes'
import MainPage from './layouts/MainPage';

const Container = styled.div`
  position: fixed;
  height: 100%;
  width: 100%;
  background: white;
  display: flex;
  justify-content: center;
  align-items: center;
`

function App() {

  return (
    <MainStateProvider>

        <Router history={history}>

          <AppWrapper />
        </Router>

    </MainStateProvider >
  );
}

export default App;


function AppWrapper() {
  const [mouseX, setMouseX] = useState(0)
  const [mouseY, setMouseY] = useState(0)
  const {state, dispatch} = useContext(mainStore)

  useEffect(() => {

    document.addEventListener('mousemove', function(e: any){
      setMouseX(e.clientX)
      setMouseY(e.clientY)
    }, false)

  }, [])

  useEffect( () => {


    const resizeFunction = () => {
      // change width from the state object
      if (window.innerWidth >= 960) {
        dispatch({type: 'setMobileOpen', mobileOpen: false});
        console.log('mobile close')

      }
      else {
        dispatch({type: 'setMobileOpen', mobileOpen: true});
        console.log('mobile open')

      }
    };

    resizeFunction()

    window.addEventListener('resize', resizeFunction);

    return () => {
      window.removeEventListener("resize", resizeFunction);
    }
  }, [])

  return (
    <Container>
      <MainPage />
    </Container>
  );
}

